<template>
	<div class="sales-board">
		<div class="sales-board-intro">
			<h2></h2>
			<p></p>
		</div>
		<div class="sales-board-form">
			<div class="sales-board-line">
				<div class="sales-board-line-left">
					
				</div>
				<div class="sales-board-line-right">
					<v-counter @on-change="onParamChange('buyNum',$event)" :max="100" :min="20"></v-counter>
				</div>
			</div>
			<div class="sales-board-line">
				<div class="sales-board-line-left">
					
				</div>
				<div class="sales-board-line-right">
					<v-mul-chooser :selections="versionList" @on-change="onParamChange('versions',$event)"></v-mul-chooser>
				</div>
			</div>
			<div class="sales-board-line">
				<div class="sales-board-line-left">
					
				</div>
				<div class="sales-board-line-right">
					<v-chooser :selections="periodList" @on-change="onParamChange('period',$event)"></v-chooser>
				</div>
			</div>
			<div class="sales-board-line">
				<div class="sales-board-line-left">
					
				</div>
				<div class="sales-board-line-right">
					{{price}} 
				</div>
			</div>
			<div class="sales-board-line">
				<div class="sales-board-line-left">&nbsp;</div>
				<div class="sales-board-line-right">
					<div class="button" @click="showPayDialog">
						
					</div>
				</div>
			</div>
		</div>
		<div class="sales-board-des">
			<h2></h2>
			<p>2020    
			</p>
		</div>
		<my-dialog :is-show="isShowPayDialog" @on-close="hidePayDialog">
			<table class="buy-dialog-table">
				<tr>
					<th></th>
					<th></th>
					<th></th>
					<th></th>
				</tr>
				<tr>
					<td>{{ buyNum }}</td>
					<td>
						<span v-for="item in versions">{{ item.label }}</span>
					</td>
					<td>{{ period.label }}</td>
					<td>{{ price }}</td>
				</tr>
			</table>
			<h3 class="buy-dialog-title"></h3>
			<bank-chooser @on-change="onChangeBanks"></bank-chooser>
			<div class="button buy-dialog-btn" @click="confirmBuy">
				
			</div>
		</my-dialog>
		<check-order :is-show-check-dialog="isShowCheckOrder" :order-id="orderId" @on-close-check-dialog="hideCheckOrder"></check-order>		
	</div>
</template>

<script>
	import VCounter from '../../components/base/counter'
	import VMulChooser from '../../components/base/multiplyChooser'
	import VChooser from '../../components/base/chooser'
	import Dialog from '../../components/base/dialog'
	import BankChooser from '../../components/bankChooser'
	import CheckOrder from '../../components/checkOrder'
	export default {
		components: {
			VCounter,
			VMulChooser,
			MyDialog: Dialog,
			VChooser,
			BankChooser,
			CheckOrder
		},
		data() {
			return {
				buyNum: 0,
				versions: [],
				period: {},
				price: 0,
				versionList: [{
						label: '',
						value: 0
					},
					{
						label: 'pdf',
						value: 1
					},
					{
						label: '',
						value: 2
					},
					{
						label: '',
						value: 3
					}
				],
				periodList: [{
						label: '',
						value: 0
					},
					{
						label: '',
						value: 1
					},
					{
						label: '',
						value: 2
					}
				],
				isShowPayDialog:false,
				bankId: null,
				orderId: null,
				isShowCheckOrder: false
			}
		},
		methods:{
			onParamChange(attr, val) {
				this[attr] = val
				this.getPrice()
			},
			getPrice() {
				//				let buyVersionsArray = _.map(this.versions, (item) => {
				//					//	let buyVersionsArray = this.versions.map( (item) => {
				//					return item.value
				//				})
				//				let reqParams = {
				//					buyNumber: this.buyNum,
				//					buyType: this.buyType.value,
				//					period: this.period.value,
				//					version: buyVersionsArray.join(',')
				//				}
				this.$http.get('../api/getPrice' /*,reqParams*/ )
					.then((res) => {
						this.price = res.body.data.amount
					})
			},
			showPayDialog() {
				this.isShowPayDialog = true
			},
			hidePayDialog() {
				this.isShowPayDialog = false
			},
			hideCheckOrder() {
				this.isShowCheckOrder = false
			},
			onChangeBanks(bankObj) {
				this.bankId = bankObj.id
			},
			confirmBuy() {
				//				let buyVersionsArray = _.map(this.versions, (item) => {
				//					//	let buyVersionsArray = this.versions.map( (item) => {
				//					return item.value
				//				})
				//				let reqParams = {
				//					buyNumber: this.buyNum,
				//					buyType: this.buyType.value,
				//					period: this.period.value,
				//					version: buyVersionsArray.join(','),
				//					bankId:this.bankId
				//				}
				this.$http.get('../api/createOrder' /* , reqParams*/ )
					.then((res) => {
						this.orderId = res.body.data.orderId
						this.isShowCheckOrder = true
						this.isShowPayDialog = false
					}, (err) => {
						this.isShowBuyDialog = false
						this.isShowErrDialog = true
					})
			}
		},
		mounted() {
			this.buyNum = 20
			this.versions = [this.versionList[0]]
			this.period = this.periodList[0]
			this.getPrice()
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.buy-dialog-title {
		font-size: 16px;
		font-weight: bold;
	}
	
	.buy-dialog-btn {
		margin-top: 20px;
	}
	
	.buy-dialog-table {
		width: 100%;
		margin-bottom: 20px;
	}
	
	.buy-dialog-table td,
	.buy-dialog-table th {
		border: 1px solid #e3e3e3;
		text-align: center;
		padding: 5px 0;
	}
	
	.buy-dialog-table th {
		background: #4fc08d;
		color: #fff;
		border: 1px solid #4fc08d;
	}
</style>


